.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Container {
  width: 100%;
}

.App-link {
  color: #61dafb;
}

.section-enter, .test-appear{
  opacity: 0;
}

.section-enter-active, .test-appear-active{
  opacity: 1;
  transition: opacity 1000ms;
}

.section-enter-done{
  opacity: 1;
}

.section-exit{
  opacity: 1;
}

.section-exit-active{
  opacity: 0;
  transition: opacity 500ms;
}

.section-exit-done{
  opacity: 0;
}